import React from 'react'
import SummaryAccountCard from "../summaryAccountCard/summaryAccountCard";
import { CircularProgress, Tooltip, TableFooter } from '@material-ui/core';
import {translate} from "../../utils/translate";
import styles from "../../containers/Riders/Tabs/tabs.module.css"
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const driverFinancialDetails = (props) => {

    let item = props.id==="driver" ? props.state.driver:props.state.rider;
    // to caculate the total of transactios amount
    let totalD =  props.state.transactions.reduce((acc,item)=>{
        return item.op==="D" ? acc+ parseFloat(item.amount): acc+0
        },0).toFixed(2)
    let totalC =  props.state.transactions.reduce((acc,item)=>{
        return item.op==="C" ? acc+ parseFloat(item.amount): acc+0
        },0).toFixed(2)
   
    return (
                <>
                <div className={styles.financialCardsWrapper}>
                    {item && item.accounts && item.accounts.map(account=>{
                        return<SummaryAccountCard onClick={props.geTransactions} key={account.id} account={account}/>
                    })}
                </div>
                {props.state.isLoadingTransactions?<div style={{display:"flex",justifyContent:"center",paddingTop:50}}>
                    <CircularProgress/>
                </div>:null}
                {!props.state.isLoadingTransactions && !props.state.isTransactionsRequested ?<div className={styles.chooseAccountText}>
                    <p>{translate("PLEASE_CHOOSE_ACCOUNT")}</p>
                </div>:null}
                {!props.state.isLoadingTransactions && props.state.isTransactionsRequested && props.state.transactions.length===0 ?<div className={styles.chooseAccountText}>
                    <p>{translate("NO_TRANSACTIONS")}</p>
                </div>:null}
                {!props.state.isLoadingTransactions && props.state.isTransactionsRequested && props.state.transactions && props.state.transactions.length>0 ?
                <Paper elevation={0} className={styles.table}>
                <div className={styles.accountTansactions}>
                     
                        <Table size="small" stickyHeader aria-label="simple table">
                            <TableHead >
                            <TableRow>
                                <TableCell style={{lineHeight:"1rem"}}>#</TableCell>
                                <TableCell style={{lineHeight:"1rem"}}>{translate("DATE")}</TableCell>
                                <TableCell style={{lineHeight:"1rem"}}>{translate("JV_NOTE")}</TableCell>
                                <TableCell style={{lineHeight:"1rem"}}>{translate("DEBTOR")}</TableCell>
                                <TableCell style={{lineHeight:"1rem"}}>{translate("CREDITOR")}</TableCell>
                                <TableCell style={{lineHeight:"1rem"}}>{translate("BALANCE")}</TableCell>
                                
                                
                            </TableRow>
                            </TableHead>
                            <TableBody>
                            {props.state.transactions.map(row => (
                                <TableRow hover key={row.id}>
                                <TableCell component="th" scope="row">{row.id} </TableCell>
                                <TableCell ><Tooltip title={row.created_at}><span>{row.created_at.slice(0,row.created_at.length-9)}</span></Tooltip></TableCell>
                                <TableCell>{row.jv_note}</TableCell>
                                <TableCell>{row.op==="D" &&row.amount}</TableCell>
                                <TableCell>{row.op==="C" &&row.amount}</TableCell>
                                <TableCell>{row.current_balance}</TableCell>
                                
                                </TableRow>
                            ))}
                            
                            
                            </TableBody>
                            <TableFooter>
                            <TableRow  key={"totalcount9455549"}>
                                <TableCell >{translate("TOTAL_AMOUNT")} </TableCell>
                                <TableCell ></TableCell>
                                <TableCell></TableCell>
                                <TableCell>{totalD}</TableCell>
                                <TableCell>{totalC}</TableCell>
                                <TableCell></TableCell> 
                            </TableRow>
                            </TableFooter>
                        </Table>
                    
                </div>
                </Paper>:null}
                </>
       
    )
}

export default driverFinancialDetails
